<template>
	<view class="filters">
		<text class="filters-title"
			@click="open">{{ selectedName === '不限' ? formatFilterConfig.filterName : selectedName }}</text>
		<uni-popup ref="popup" background-color="#fff" :class="{ 'popup-height': type === 'left' || type === 'right' }">
			<view class="popup-content">
				<view class="title">{{ title }}</view>
				<view class="list">
					<view class="item" :class="item.id === select ? 'cur' : ''"
						v-for="item in formatFilterConfig.filterList" :key="item.id">
						<text class="text" @click="select = item.id">{{ item.name }}</text>
					</view>
				</view>
				<view class="btn" @click="handleClick">确定</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	export default {
		name: 'IndexFilters',
		props: {
			title: {
				type: String,
				default: ''
			},
			filtersConfig: {
				type: Object,
				default: () => {}
			},
			type: {
				type: String,
				default: 'top'
			},
			selectAllText: {
				type: String,
				default: '不限'
			}
		},
		data() {
			return {
				formatFilterConfig: {},
				selected: '',
				select: ''
			}
		},
		computed: {
			selectedName() {
				return this.formatFilterConfig?.filterList?.find(item => item.id === this.selected)?.name || ''
			}
		},
		created() {
			this.formatFilterConfig = {
				...this.filtersConfig,
				filterList: [{
					name: this.selectAllText,
					id: '0'
				}, ...this.filtersConfig.filterList]
			}
			this.selected = this.formatFilterConfig.filterList[0].id
			this.select = this.selected
		},
		methods: {
			open() {
				this.select = this.selected
				this.$refs.popup.open(this.type)
			},
			close() {
				this.$refs.popup.close()
			},
			handleClick() {
				this.selected = this.select
				this.close()
				this.$emit('onclick')
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import '../../../assets/css/common.scss';

	.filters {
		&-title {
			position: relative;
			padding: 0 50rpx 0 10rpx;
			cursor: pointer;

			&:after {
				display: block;
				content: '';
				width: 30rpx;
				height: 30rpx;
				background: url('data:image/png;base64,');
				background-size: 100% 100%;
				position: absolute;
				right: 10rpx;
				top: 0;
				bottom: 0;
				margin: auto;
			}
		}
	}

	.popup-content {
		padding: 40rpx 22rpx;

		.title {
			font-size: 32rpx;
			font-weight: bold;
			color: $primaryColor;
			padding: 0 10rpx;
			text-align: left;
		}

		.list {
			display: flex;
			flex-wrap: wrap;
			text-align: center;

			.item {
				flex-basis: 25%;
				line-height: 60rpx;
				margin: 30rpx 0 0 0;

				.text {
					margin: 0 15rpx;
					border: #ddd 1px solid;
					border-radius: 10rpx;
					display: block;
					cursor: pointer;
				}

				&:hover,
				&.cur {
					.text {
						color: $primaryColor;
						background-color: rgba($primaryColor, 0.1);
						border-color: $primaryColor;
					}
				}
			}
		}

		.btn {
			box-sizing: border-box;
			text-align: center;
			background: $primaryColor;
			border-radius: 10rpx;
			line-height: 80rpx;
			color: #fff;
			margin: 50rpx 10rpx 0 10rpx;
			font-size: 32rpx;
			cursor: pointer;
		}
	}
</style>